import { NgModule, ErrorHandler }		                                      from '@angular/core';
import { HttpClient, HttpClientModule }                                       from '@angular/common/http';
import { BrowserModule }                                                      from '@angular/platform-browser';
import { FormsModule, ReactiveFormsModule }   								  from '@angular/forms';

import { IonicApp, IonicModule, IonicErrorHandler, DeepLinkConfig }           from 'ionic-angular';
import { StatusBar }                                                          from '@ionic-native/status-bar';
import { SuperTabsModule }                                                    from 'ionic2-super-tabs';
import { AgmCoreModule }                                                      from '@agm/core';
import { ChartsModule }                                                       from 'ng2-charts';
import { TranslateModule, TranslateLoader }                                   from '@ngx-translate/core';
import { TranslateHttpLoader }                                                from '@ngx-translate/http-loader';


import { C8o }                                                                from "c8osdkangular";
import { C8oRouter } 			                                              from 'c8ocaf';
import { ActionBeans }                                                        from '../services/actionbeans.service';

import { MyApp } 				                                              from './app.component';
import { PLANTS } from "../pages/PLANTS/plants";
import { MACHINES } from "../pages/MACHINES/machines";
import { DETAILS } from "../pages/DETAILS/details";
import { INTERVENTION } from "../pages/INTERVENTION/intervention";



/**
 * Deep links to your pages so that the app can rout directly to the page url
 */
export const deepLinkConfig: DeepLinkConfig = {
  links: [ { component: PLANTS, name: "PLANTS", segment: "Page" }, { component: MACHINES, name: "MACHINES", segment: "MACHINES" }, { component: DETAILS, name: "DETAILS", segment: "DETAILS" }, { component: INTERVENTION, name: "INTERVENTION", segment: "INTERVENTION" }]
};


/**
 * Customize the ngx-translate loader for assets/i18n
 */
export function createTranslateLoader(http: HttpClient) {
    return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

@NgModule({
  declarations: [
    MyApp, PLANTS, MACHINES, DETAILS, INTERVENTION
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
	FormsModule,
	ReactiveFormsModule,
	ChartsModule,
	TranslateModule.forRoot({
        loader: {
          provide: TranslateLoader,
          useFactory: (createTranslateLoader),
          deps: [HttpClient]
        }
	}),
	SuperTabsModule.forRoot(),
	AgmCoreModule.forRoot({
	      apiKey: 'AIzaSyB0Nl1dX0kEsB5QZaNf6m-tnb1N-U5dpXs'
	}),
    IonicModule.forRoot(MyApp, {}, deepLinkConfig)
  ],
  bootstrap: [IonicApp],
  entryComponents: [MyApp, PLANTS, MACHINES, DETAILS, INTERVENTION],
  providers: [
    StatusBar,
    C8o,
    C8oRouter,
    ActionBeans,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})

export class AppModule {}


